export const paymentResult = (payment_from: number, payment_to: number, currency: string) => {
  if (payment_from > 0 && payment_to === 0) {
    return `/  ${payment_from} ${currency}`
  } else if (payment_from === 0 && payment_to > 0) {
    return `/  ${payment_to} ${currency}`
  } else if (payment_from > 0 && payment_to > 0 && payment_to !== payment_from) {
    return `/ ${payment_from} - ${payment_to} ${currency}`
  } else if (payment_from === payment_to && payment_to + payment_from !== 0) {
    return `/ ${payment_from}`
  }
}
